@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap");
@keyframes gradient {
  0% {
    background-position: 10% 90%; }
  50% {
    background-position: 30% 70%; }
  70% {
    background-position: 50% 50%; }
  100% {
    background-position: 20% 80%; } }

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  padding: 50px 20px;
  font-family: "DM Sans", sans-serif;
  box-sizing: border-box; }

.main-container {
  margin: 0 auto;
  width: 100%;
  max-width: 1432px;
  max-height: 764px; }
  .main-container__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr); }

.heading {
  color: #2C346D;
  margin-bottom: 5px; }

.heading,
.paragraph {
  color: #2C346D; }

span {
  color: #3343BB; }

.btn-primary {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 187px;
  height: 45px;
  font-size: 14px;
  font-weight: bold;
  color: #fff;
  background-color: #3343BB;
  line-height: 24px;
  border-radius: 6px;
  text-decoration: none; }

.form {
  margin-bottom: 22px; }
  .form__radio-group {
    position: relative;
    padding: 10px 0 10px 18px;
    width: 100%;
    max-width: 440px;
    height: 45px;
    background-color: transparent;
    transition: background-color .5s ease;
    border-radius: 6px; }
    .form__radio-group.subscribe {
      margin-bottom: 2px; }
    .form__radio-group.active, .form__radio-group:hover {
      background-color: #EDF0FC; }
  .form__radio-input {
    display: none; }
    .form__radio-input:checked ~ .form__label-radio .form__radio-button::after {
      opacity: 1; }
  .form__label-radio {
    position: relative;
    padding-left: 32px;
    font-weight: bold;
    font-size: 14px;
    line-height: 24px;
    color: #3343BB;
    cursor: pointer; }
  .form__radio-button {
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    height: 20px;
    width: 20px;
    border: 2px solid #3343BB;
    border-radius: 50%; }
    .form__radio-button::after {
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: #3343BB;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      opacity: 0;
      transition: opacity .2s; }

.subs-wrapper {
  display: inline-block;
  margin-left: 30px;
  width: 74px;
  border: none;
  border-bottom: 1px solid #3343BB;
  background-color: transparent; }
  .subs-wrapper input {
    width: 100%;
    font-size: 14px;
    font-weight: 700;
    border: none;
    background-color: transparent;
    color: #3343BB;
    cursor: pointer; }
    .subs-wrapper input::placeholder {
      color: #3343BB; }
  .subs-wrapper__list {
    display: none;
    position: absolute;
    top: 38px;
    right: 0;
    margin: 0;
    width: 140px;
    height: auto;
    background-color: #EDF0FC;
    border-radius: 6px;
    z-index: 999; }
    .subs-wrapper__list li {
      padding: 10px 32px;
      font-size: 16px;
      cursor: pointer;
      list-style: none;
      color: #3343BB;
      transition: opacity .3s; }
      .subs-wrapper__list li:hover {
        opacity: .8; }
  .subs-wrapper::after {
    content: "";
    position: absolute;
    right: 34px;
    top: 16px;
    pointer-events: none;
    border: solid #3343BB;
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(45deg); }

.ratings {
  display: flex;
  margin-bottom: 20px; }
  .ratings .stars {
    margin-right: 24px; }
    .ratings .stars span {
      display: inline-block;
      position: relative;
      width: 12px;
      color: #2C346D; }
      .ratings .stars span:not(:last-of-type) {
        margin-right: 12px; }
        .ratings .stars span:not(:last-of-type)::before {
          content: "\2605";
          position: absolute; }
  .ratings .reviews {
    font-size: 14px;
    font-weight: 700;
    line-height: 24px; }

.add-to-cart {
  display: flex; }
  .add-to-cart .counter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 16px;
    width: 100%;
    max-width: 177px;
    height: 45px;
    color: #3343BB;
    border: 1px solid #3343BB;
    border-radius: 6px; }
    .add-to-cart .counter__value {
      width: 20px;
      font-size: 14px;
      font-weight: bold;
      color: #3343BB;
      background-color: transparent;
      border: none;
      text-align: center;
      line-height: 24px; }
    .add-to-cart .counter span {
      padding: 0 10px;
      font-weight: bold;
      font-size: 20px;
      cursor: pointer; }

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

@keyframes gradient {
  0% {
    background-position: 10% 90%; }
  50% {
    background-position: 30% 70%; }
  70% {
    background-position: 50% 50%; }
  100% {
    background-position: 20% 80%; } }

.main-container__content__right {
  padding: 70px 122px 98px 82px;
  background: linear-gradient(45deg, #f0d6ad, #f3e9e0, #fff);
  background-size: 300% 100%;
  animation: gradient 7s ease infinite; }
  .main-container__content__right .breadcrumbs {
    margin-bottom: 3px; }
    .main-container__content__right .breadcrumbs span {
      font-size: 12px;
      font-weight: 700;
      line-height: 20px; }
  .main-container__content__right .heading {
    font-size: 62px;
    line-height: 64px;
    font-weight: 700; }
  .main-container__content__right .content {
    width: 100%;
    max-width: 368px; }
    .main-container__content__right .content .paragraph {
      margin-bottom: 32px;
      font-size: 14px;
      line-height: 22px; }

.main-container__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 72px 0 80px;
  height: 76px; }
  .main-container__info .product,
  .main-container__info .subscribe {
    display: flex;
    width: 100%; }
  .main-container__info .product {
    align-items: center; }
    .main-container__info .product__img {
      margin-right: 16px;
      width: 56px;
      height: 56px; }
    .main-container__info .product__info h3,
    .main-container__info .product__info span {
      color: #2C346D; }
    .main-container__info .product__info h3 {
      font-size: 20px;
      line-height: 28px;
      text-transform: capitalize; }
    .main-container__info .product__info span {
      font-size: 12px;
      line-height: 18px;
      font-weight: bold;
      text-decoration-line: underline; }
  .main-container__info .subscribe {
    justify-content: flex-end; }
    .main-container__info .subscribe__info {
      margin-right: 25px;
      text-align: right; }
      .main-container__info .subscribe__info span {
        display: block;
        padding-right: 5px;
        font-size: 14px;
        font-weight: 700;
        line-height: 20px; }
        .main-container__info .subscribe__info span:last-of-type {
          padding-right: 0;
          font-size: 12px; }
